:root {
  --point-color: #ff0000;
  --gray-color: #666;
  --background-color: #000;
}

.slide_cover {
  width: 100vw;
  height: 600px;
  background-color: #000;
}

.slide_cover > .slide {
  position: relative;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 100%;
  max-width: 1300px;
  height: 100%;
  background-color: #eee;
}

.slide_cover > .slide::before {
  content: "";
  position: absolute;
  top: 0;
  display: block;
  width: 10%;
  height: 100%;
  background: -webkit-gradient(linear, left top, right top, from(black), color-stop(40%, rgba(0, 0, 0, 0.5)), to(rgba(0, 0, 0, 0)));
  background: linear-gradient(to right, black 0%, rgba(0, 0, 0, 0.5) 40%, rgba(0, 0, 0, 0) 100%);
  left: 0;
}

.slide_cover > .slide::after {
  content: "";
  position: absolute;
  top: 0;
  display: block;
  width: 10%;
  height: 100%;
  background: -webkit-gradient(linear, right top, left top, from(black), color-stop(40%, rgba(0, 0, 0, 0.5)), to(rgba(0, 0, 0, 0)));
  background: linear-gradient(to left, black 0%, rgba(0, 0, 0, 0.5) 40%, rgba(0, 0, 0, 0) 100%);
  right: 0;
}

.slide_cover .slide_text {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  width: 100%;
  height: 100%;
}

.slide_cover .slide_text p {
  width: 70%;
}

.slide_cover .slide_text > .title {
  font-size: 3em;
  margin-bottom: 10px;
}

.slide_cover .slide_text > .desc {
  margin-bottom: 50px;
}

.slide_cover .slide_text > .rating {
  font-size: 2em;
  margin-bottom: 100px;
  line-height: 0.5em;
}

.slide_cover .slide_text > .rating span {
  margin-left: 10px;
}

.slide_cover .slide_text > .ticketing span {
  display: inline-block;
  text-align: center;
  width: 130px;
  height: 50px;
  line-height: 50px;
  border: 1px solid var(--gray-color);
  background-color: rgba(255, 255, 255, 0.6);
  cursor: pointer;
}
