@import "../common.scss";

:root {
  --logo-width: 200px;
  --btn-small-padding: 5px 10px;
}

nav {
  width: 100vw;
  background-color: #fff;
  border-bottom: 1px solid #ddd;

  > div {
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;
    max-width: 1200px;
    color: var(--gray-color);
    background-color: #fff;
    //   @extend %exborder;

    .main_logo {
      display: flex;
      width: var(--logo-width);
      height: auto;
      margin: 0;
    }

    li:hover {
      cursor: pointer;
    }

    > .menu {
      display: flex;
      width: calc(100% - var(--logo-width));
      padding-left: 3%;

      justify-content: space-between;
      > .category {
        width: 60%;
        margin: 0;
        > li {
          float: left;
          padding: var(--btn-small-padding);
          margin-right: 10px;
          font-size: 1.2em;
          font-weight: 700;
          &:hover {
            color: var(--point-color);
          }
        }
      }
      > .login_menu {
        width: 40%;
        margin: 0;
        > li {
          float: right;
          margin-left: 5px;
          padding: var(--btn-small-padding);
          &:hover {
            color: #000;
          }
        }
      }
    }
  }
}
