:root {
  --point-color: #ff0000;
  --gray-color: #666;
  --background-color: #000;
}

//@extend %exborder;
%exborder {
  border: 1px solid red;
}

@mixin linear-gra-black($to) {
  content: "";
  position: absolute;
  top: 0;
  display: block;
  width: 10%;
  height: 100%;
  background: linear-gradient(
    $to,
    rgba(0, 0, 0, 1) 0%,
    rgba(0, 0, 0, 0.5) 40%,
    rgba(0, 0, 0, 0) 100%
  );
}
