:root {
  --point-color: #ff0000;
  --gray-color: #666;
  --background-color: #000;
}

:root {
  --logo-width: 200px;
  --btn-small-padding: 5px 10px;
}

nav {
  width: 100vw;
  background-color: #fff;
  border-bottom: 1px solid #ddd;
}

nav > div {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 100%;
  max-width: 1200px;
  color: var(--gray-color);
  background-color: #fff;
}

nav > div .main_logo {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: var(--logo-width);
  height: auto;
  margin: 0;
}

nav > div li:hover {
  cursor: pointer;
}

nav > div > .menu {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: calc(100% - var(--logo-width));
  padding-left: 3%;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

nav > div > .menu > .category {
  width: 60%;
  margin: 0;
}

nav > div > .menu > .category > li {
  float: left;
  padding: var(--btn-small-padding);
  margin-right: 10px;
  font-size: 1.2em;
  font-weight: 700;
}

nav > div > .menu > .category > li:hover {
  color: var(--point-color);
}

nav > div > .menu > .login_menu {
  width: 40%;
  margin: 0;
}

nav > div > .menu > .login_menu > li {
  float: right;
  margin-left: 5px;
  padding: var(--btn-small-padding);
}

nav > div > .menu > .login_menu > li:hover {
  color: #000;
}
