@import "../../common.scss";

.slide_cover {
  width: 100vw;
  height: 600px;
  background-color: #000;

  > .slide {
    position: relative;
    align-items: center;
    width: 100%;
    max-width: 1300px;
    height: 100%;
    background-color: #eee;

    &::before {
      @include linear-gra-black(to right);
      left: 0;
    }
    &::after {
      @include linear-gra-black(to left);
      right: 0;
    }
  }

  .slide_text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    height: 100%;

    p {
      width: 70%;
    }

    > .title {
      font-size: 3em;
      margin-bottom: 10px;
    }

    > .desc {
      margin-bottom: 50px;
    }

    > .rating {
      font-size: 2em;
      margin-bottom: 100px;
      line-height: 0.5em;
      span {
        margin-left: 10px;
      }
    }

    > .ticketing {
      span {
        display: inline-block;
        text-align: center;
        width: 130px;
        height: 50px;
        line-height: 50px;
        border: 1px solid var(--gray-color);
        background-color: rgba(255, 255, 255, 0.6);
        cursor: pointer;
      }
    }
  }
}
