@import "../../common.scss";

.main_loading {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  span {
    margin-top: 20px;
    color: var(--gray-color);
  }
}

.importent_btn {
  display: flex;
  width: 100vw;
  height: 100px;
  background-color: #000;
  > ul {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 100%;
    max-width: 1200px;
    // padding: 0 50px;  사이즈 줄었을때

    > li {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-grow: 4;
      height: 50%;
      color: #fff;
      border: 1px solid #fff;
      margin-right: 3%;
      cursor: pointer;

      &:last-child {
        margin-right: 0;
      }
      &:nth-child(1) {
        flex-grow: 1;
      }
    }
  }
}
.popular_section {
  width: 100vw;
  background: #000;
}

.popular_cover {
  position: relative;
  width: 100vw;
  max-width: 1200px;
  padding: 20px 0px 50px;
  text-align: center;
  background: url(../../assets/popular_bg.jpg) no-repeat no-repeat center center;
  &::before {
    @include linear-gra-black(to right);
    left: 0;
  }
  &::after {
    @include linear-gra-black(to left);
    right: 0;
  }

  * {
    z-index: 100;
  }

  > .top4_title {
    font-size: 2.5em;
    padding: 50px 0 30px;
    color: #fff;
  }
  > .top4_item {
    display: flex;
  }

  .popular {
    position: relative;
    width: calc(100% / 4);

    /* TOP4 영화 이미지 박스 */

    .rankNum {
      position: absolute;
      z-index: 1000;
      top: -3px;
      left: 21px;
      width: 35px;
      height: 35px;
      color: #fff;
      background: var(--point-color);
      border-radius: 0 0 25px 0;
      font-size: 1.5em;
      box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.6);
    }

    .img_cover {
      position: relative;
      width: 250px;
      height: 375px;
      overflow: hidden;
      box-shadow: 0 0 10px #000;

      &:hover > p {
        top: 0;
        z-index: 10;
        border-bottom: 1px solid #333;
        transition: all 0.5s ease-out;
      }
    }

    .movie_summary {
      position: absolute;
      top: -325px;
      padding: 50px 20px 20px;
      height: calc(100% - 50px - 70px);
      word-break: break-all;
      color: #fff;
      background-color: rgba(0, 0, 0, 0.8);
      transition: all 0.5s ease-in;
    }

    .movie_img {
      width: 100%;
      height: 100%;
    }

    .like_ticketing {
      position: absolute;
      display: flex;
      width: 100%;
      height: 50px;
      bottom: 0;
      z-index: 5;
      background-color: rgba(0, 0, 0, 0.8);

      div {
        cursor: pointer;
      }

      > .like_btn {
        width: 60px;
        height: 100%;

        &:hover path {
          stroke: var(--point-color);
          fill: rgba(#ff0000, 0.3);
        }

        &.liked:hover path {
          stroke: var(--point-color);
          fill: rgba(#ff0000, 1);
        }

        &:active path {
          fill: rgba(#ff0000, 1);
        }

        path {
          stroke-width: 30px;
          stroke: var(--point-color);
        }

        .like_heart {
          width: 50%;
        }
      }

      > .ticketing_btn {
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: calc(100% - 50px);
        color: var(--gray-color);

        span {
          display: inline-block;
          width: 100%;
          height: 30px;
          line-height: 30px;
          border-left: 1px solid #fff;
        }

        &:hover {
          color: #fff;
        }
      }
    }

    /* TOP4 영화 제목 / 징르 */
    .movie_title {
      width: 250px;
      padding: 20px;
      font-size: 1.2em;
      color: #fff;
      font-weight: 500;
      text-shadow: #000 0 0 5px;
    }

    .genres_cover {
      display: flex;
      margin: 0 auto;

      .genres {
        display: flex;
        flex-wrap: wrap;
        justify-content: left;

        li {
          flex-grow: 1;
          flex-shrink: 1;
          font-size: 0.8em;
          padding: 3px 5px;
          margin: 0 3px 10px;
          border-radius: 5px;
          border: 1px solid var(--gray-color);
          font-weight: 100;
          color: #fff;
          background: rgba(255, 255, 255, 0.2);
        }
      }
    }
  }
}
