@charset "UTF-8";
:root {
  --point-color: #ff0000;
  --gray-color: #666;
  --background-color: #000;
}

.main_loading {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 100vw;
  height: 100vh;
}

.main_loading span {
  margin-top: 20px;
  color: var(--gray-color);
}

.importent_btn {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100vw;
  height: 100px;
  background-color: #000;
}

.importent_btn > ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 100%;
  height: 100%;
  max-width: 1200px;
}

.importent_btn > ul > li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-flex: 4;
      -ms-flex-positive: 4;
          flex-grow: 4;
  height: 50%;
  color: #fff;
  border: 1px solid #fff;
  margin-right: 3%;
  cursor: pointer;
}

.importent_btn > ul > li:last-child {
  margin-right: 0;
}

.importent_btn > ul > li:nth-child(1) {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
}

.popular_section {
  width: 100vw;
  background: #000;
}

.popular_cover {
  position: relative;
  width: 100vw;
  max-width: 1200px;
  padding: 20px 0px 50px;
  text-align: center;
  background: url(../../assets/popular_bg.jpg) no-repeat no-repeat center center;
}

.popular_cover::before {
  content: "";
  position: absolute;
  top: 0;
  display: block;
  width: 10%;
  height: 100%;
  background: -webkit-gradient(linear, left top, right top, from(black), color-stop(40%, rgba(0, 0, 0, 0.5)), to(rgba(0, 0, 0, 0)));
  background: linear-gradient(to right, black 0%, rgba(0, 0, 0, 0.5) 40%, rgba(0, 0, 0, 0) 100%);
  left: 0;
}

.popular_cover::after {
  content: "";
  position: absolute;
  top: 0;
  display: block;
  width: 10%;
  height: 100%;
  background: -webkit-gradient(linear, right top, left top, from(black), color-stop(40%, rgba(0, 0, 0, 0.5)), to(rgba(0, 0, 0, 0)));
  background: linear-gradient(to left, black 0%, rgba(0, 0, 0, 0.5) 40%, rgba(0, 0, 0, 0) 100%);
  right: 0;
}

.popular_cover * {
  z-index: 100;
}

.popular_cover > .top4_title {
  font-size: 2.5em;
  padding: 50px 0 30px;
  color: #fff;
}

.popular_cover > .top4_item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.popular_cover .popular {
  position: relative;
  width: calc(100% / 4);
  /* TOP4 영화 이미지 박스 */
  /* TOP4 영화 제목 / 징르 */
}

.popular_cover .popular .rankNum {
  position: absolute;
  z-index: 1000;
  top: -3px;
  left: 21px;
  width: 35px;
  height: 35px;
  color: #fff;
  background: var(--point-color);
  border-radius: 0 0 25px 0;
  font-size: 1.5em;
  -webkit-box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.6);
          box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.6);
}

.popular_cover .popular .img_cover {
  position: relative;
  width: 250px;
  height: 375px;
  overflow: hidden;
  -webkit-box-shadow: 0 0 10px #000;
          box-shadow: 0 0 10px #000;
}

.popular_cover .popular .img_cover:hover > p {
  top: 0;
  z-index: 10;
  border-bottom: 1px solid #333;
  -webkit-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
}

.popular_cover .popular .movie_summary {
  position: absolute;
  top: -325px;
  padding: 50px 20px 20px;
  height: calc(100% - 50px - 70px);
  word-break: break-all;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.8);
  -webkit-transition: all 0.5s ease-in;
  transition: all 0.5s ease-in;
}

.popular_cover .popular .movie_img {
  width: 100%;
  height: 100%;
}

.popular_cover .popular .like_ticketing {
  position: absolute;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 50px;
  bottom: 0;
  z-index: 5;
  background-color: rgba(0, 0, 0, 0.8);
}

.popular_cover .popular .like_ticketing div {
  cursor: pointer;
}

.popular_cover .popular .like_ticketing > .like_btn {
  width: 60px;
  height: 100%;
}

.popular_cover .popular .like_ticketing > .like_btn:hover path {
  stroke: var(--point-color);
  fill: rgba(255, 0, 0, 0.3);
}

.popular_cover .popular .like_ticketing > .like_btn.liked:hover path {
  stroke: var(--point-color);
  fill: red;
}

.popular_cover .popular .like_ticketing > .like_btn:active path {
  fill: red;
}

.popular_cover .popular .like_ticketing > .like_btn path {
  stroke-width: 30px;
  stroke: var(--point-color);
}

.popular_cover .popular .like_ticketing > .like_btn .like_heart {
  width: 50%;
}

.popular_cover .popular .like_ticketing > .ticketing_btn {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  width: calc(100% - 50px);
  color: var(--gray-color);
}

.popular_cover .popular .like_ticketing > .ticketing_btn span {
  display: inline-block;
  width: 100%;
  height: 30px;
  line-height: 30px;
  border-left: 1px solid #fff;
}

.popular_cover .popular .like_ticketing > .ticketing_btn:hover {
  color: #fff;
}

.popular_cover .popular .movie_title {
  width: 250px;
  padding: 20px;
  font-size: 1.2em;
  color: #fff;
  font-weight: 500;
  text-shadow: #000 0 0 5px;
}

.popular_cover .popular .genres_cover {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 0 auto;
}

.popular_cover .popular .genres_cover .genres {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: left;
      -ms-flex-pack: left;
          justify-content: left;
}

.popular_cover .popular .genres_cover .genres li {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  -ms-flex-negative: 1;
      flex-shrink: 1;
  font-size: 0.8em;
  padding: 3px 5px;
  margin: 0 3px 10px;
  border-radius: 5px;
  border: 1px solid var(--gray-color);
  font-weight: 100;
  color: #fff;
  background: rgba(255, 255, 255, 0.2);
}
